import ApiInfo from './ApiInfo.js';

class QutuibaApiInfo extends ApiInfo {
    constructor({
                    path,
                    type = 'get',
                    timeout = 5000,
                    headers,
                    project = 'qutuiba',
                    author = '未定义'
                }) {
        super({
            path,
            type,
            timeout,
            headers,
            project,
            author
        });
    }
}

export default {
    version: {
        getList: new QutuibaApiInfo({
            path: '/p/select?c=payrolltrans.empcode,employee.fullchinesename,payrolltrans.payrollstatus,employee.companycode,payrolltrans.periodcode&g=payrolltrans.payrollstatus,payrolltrans.empcode,employee.fullchinesename,employee.companycode,payrolltrans.periodcode',
            type: 'post',
            author: ' '
        }),
        calctrans: new QutuibaApiInfo({
            path: '/p/calctrans',
            type: 'post',
            author: ' '
        }),
        getPkgList: new QutuibaApiInfo({
            path: '/p/preview',
            type: 'get',
            author: ' '
        }),
        commit: new QutuibaApiInfo({
            path: '/p/commit',
            type: 'post',
            author: ' '
        }),

        add: new QutuibaApiInfo({
            path: '/admin/appversion/add',
            type: 'post',
            author: ' '
        }),
        getAnother: new QutuibaApiInfo({
        path: '/p/select?c= employee.empcode,employee.fullchinesename',
        type: 'post',
        author: ' '
        })
    },
    domain: {
        getList: new QutuibaApiInfo({
            path: '/p/select?c=Company.CompanyCode,Company.CompanyName,Company.Address,Company.Phone,Company.Fax,Company.PostalAddress,Company.EmprTaxFileNum,Company.CurrencyCode,Company.PayDay,Company.PeriodType,Company.IsActive,Company.CreateDate',
            type: 'get',
            author: ' '
        }),
    },
    upload: {
        uploadFile: new QutuibaApiInfo({
            path: '/import/csv',
            type: 'post',
            author: ' '
        })
    },
    member: {
        logout: new QutuibaApiInfo({
            path: '/u/logout',
            type: 'post',
            author: ' '
        }),
        employList: new QutuibaApiInfo({
            path: '/p/select?c=Employee.CompanyCode,Employee.EmpCode,Employee.IsPartime,Employee.EmploymentStartDate,Employee.EmploymentEndDate,Employee.EmploymentType,Employee.SurName,Employee.OtherName,Employee.FullChineseName,Employee.Gender,Employee.MobilePhone,Employee.HomePhone,Employee.BirthDate,Employee.Email,Employee.ICN,Employee.Passport,Employee.Country,Employee.MaritalStatus,Employee.SpouseName,Employee.SpouseICN,Employee.SpousePassport,Employee.SpouseCountry,Employee.ResidentialAddress,Employee.PostalAddress,Employee.BankAccount,Employee.Title,Employee.PaySlipDeliveryDay,Employee.IsActive,Employee.CreateDate,Employee.UpdateDate',
            type: 'get',
            author: ' '
        }),
        update: new QutuibaApiInfo({
            path: '/p/update',
            type: 'post',
            author: ' '
        }),

        login: new QutuibaApiInfo({
            path: '/u/login',
            type: 'post',
            author: ' '
        }),
        pay: new QutuibaApiInfo({
            path: '/p/select?c=PayItem.PayItemUid,Employee.EmpCode,Employee.FullChineseName,PayItem.Templatecode, PayItem.CompanyCode, PayItem.Amount, PayItem.Function, PayItem.EffectiveDate&o=Employee.EmpCode',
            type: 'post',
            author: ' '
        }),
        package: new QutuibaApiInfo({
            path: '/p/select?c=PackageItem.PackageItemUid,Employee.EmpCode,Employee.FullChineseName,PackageItem.Templatecode,PackageItem.CompanyCode,PackageItem.Amount,PackageItem.Function,PackageItem.EffectiveDate,PackageItem.PackageUid&o=Employee.EmpCode,PackageItem.PackageUid',
            type: 'get',
            author: ' '
        }),
    },
    cdn: {
        getCdnList: new QutuibaApiInfo({
            path: '/p/select?c=period.*,payrolltrans.transuid',
            type: 'post',
            author: ' '
        }),
        newperiod: new QutuibaApiInfo({
            path: '/p/newperiod',
            type: 'post',
            author: ' '
        }),
        getCdn: new QutuibaApiInfo({
            path: '/p/select?c=top 15,payrolltrans.PeriodCode,period.PeriodType,period.CompanyCode,period.StartDate,period.EndDate,$count(1) as Transactions&o=_period.StartDate&g=payrolltrans.periodcode,period.periodtype,period.companycode,period.startdate,period.enddate',
            type: 'post',
            author: ' '
        }),

    },
}




